import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import Highlighter from 'react-highlight-words';
import {
  Form,
  Breadcrumb,
  Tabs,
  Table,
  Popconfirm,
  Tooltip,
  Button,
  Modal,
  Row,
  Col,
  Input,
  message,
  Select,
  Typography,
  Icon,
  Switch,
  InputNumber,
} from 'antd';
import styled from 'styled-components';
import { injectIntl } from 'react-intl';
import {
  getEngines,
  getServices,
  addEngine,
  deleteEngine,
  enabledEngine,
  disabledEngine,
  enginesByUser,
  addUserEngine,
  delUserEngine,
  grantAll,
  cloneModel,
} from '../../services';

const { Title, Text } = Typography;
const { Option } = Select;
const { TabPane } = Tabs;
const FormItem = Form.Item;
const ButtonAction = styled(Button)`
  margin: 5px;
`;

