const EngineCmp = props => {
  const {
    user,
    form,
    form: { getFieldDecorator },
    match: {
      params: { userId },
    },
    intl: { messages },
  } = props;

  const [visibleEdit, setVisibleEdit] = useState(false);
  const [visible, setVisible] = useState(false);
  const [father, setFather] = useState(null);
  const [children, setChildren] = useState([]);
  const [mode, setMode] = useState('add');
  const [loading, setLoading] = useState(true);
  const [loadingServices, setLoadingServices] = useState(true);
  const [engines, setEngines] = useState([]);
  const [userActive, setUserActive] = useState(null);
  const [userEngines, setUserEngines] = useState([]);
  const [services, setServices] = useState([]);
  const [searchText, setSearchText] = useState('');
  const [searchInput, setSearchInput] = useState('');
  const [current, setCurrent] = useState({
    descr: '',
    grant_all: 0,
    id: null,
    model: '',
    src: '',
    status: 0,
    tgt: '',
  });

  const getService = text => {
    let result = null;
    if (services.length) {
      let serv = services.filter(item => item.id === text);
      if (serv.length) {
        result = serv[0].name;
      }
    }
    return result;
  };

  useEffect(() => {
    fetchEngines();
    fetchServices();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchEngines = async () => {
    try {
      let response;
      if (user.rol === 'client') {
        response = await enginesByUser(user.id);
        const {
          data: { engines },
        } = response;
        setEngines(engines);
      } else {
        response = await getEngines();
        const {
          data: { engines },
        } = response;
        setEngines(engines);
      }

      if (userId) {
        fetchUserEngines();
      } else {
        setLoading(false);
      }
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const fetchUserEngines = async () => {
    try {
      const response = await enginesByUser(userId);
      const {
        data,
        data: { engines },
      } = response;
      setUserEngines(engines);
      setUserActive(data.user);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const fetchServices = async () => {
    try {
      const response = await getServices();
      const {
        data: { services },
      } = response;
      setServices(services);
      setLoadingServices(false);
    } catch (error) {
      setLoadingServices(false);
      message.error(error.message);
    }
  };

  const delEngine = async model => {
    try {
      setLoading(true);
      await deleteEngine({ id: model.id });
      message.success(messages.default.success);
      fetchEngines();
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const userEng = async (item, exist) => {
    try {
      setLoading(true);
      if (exist) {
        await delUserEngine({ id: userId, engine: item.id });
      } else {
        await addUserEngine({ id: userId, engine: item.id });
      }
      fetchEngines();
      message.success(messages.default.success);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const enaEngine = async node => {
    try {
      setLoading(true);
      await enabledEngine({ id: node.id });
      setLoading(false);
      message.success(messages.default.success);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const disEngine = async node => {
    try {
      setLoading(true);
      await disabledEngine({ id: node.id });
      setLoading(false);
      message.success(messages.default.success);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err || (mode === 'clone' && err.serviceid && err.src && err.tgt)) {
        const form = {
          serviceid: values.serviceid,
          inserviceid: values.inserviceid,
          src: values.src,
          tgt: values.tgt,
          descr: values.descr,
          status: values.status,
          domain: values.domain,
          flavor: values.flavor,
        };
        setLoading(true);

        if (mode === 'add') {
          try {
            await addEngine(form);
            message.success(messages.default.success);
            handleReset();
            setVisible(false);
            fetchEngines();
          } catch (error) {
            message.error(error.message);
          }
        }
      }
    });
  };

  const grant = async record => {
    try {
      setLoading(true);
      await grantAll({
        engine_id: record.id,
        grant_all: record.grant_all ? 0 : 1,
      });
      fetchEngines();
      message.success(messages.default.success);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const getColumnSearchProps = (dataIndex, service = false) => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            setSearchInput(node);
            // this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => handleSearchReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) => {
      if (service) {
        return getService(record[dataIndex])
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase());
      } else {
        return record[dataIndex]
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase());
      }
    },

    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  });

  const handleSearch = (selectedKeys, confirm) => {
    confirm();
    setSearchText(selectedKeys[0]);
  };

  const handleSearchReset = clearFilters => {
    clearFilters();
    setSearchText('');
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: messages.engines.descr,
      dataIndex: 'descr',
      key: 'descr',
      render: (text, record) => <span>{text}</span>,
      // ...getColumnSearchProps('descr'),
    },
    {
      title: messages.engines.service,
      dataIndex: 'serviceid',
      key: 'serviceid',
      sorter: (a, b) => {
        return getService(a.serviceid).length - getService(b.serviceid).length;
      },
      sortDirections: ['descend'],
      ...getColumnSearchProps('serviceid', true),
      render: text => {
        if (services.length) {
          let serv = services.filter(item => item.id === text);
          if (serv.length) {
            return serv[0].name;
          }
        }
      },
    },
    {
      title: messages.engines.src,
      dataIndex: 'src',
      key: 'src',
      sorter: (a, b) => {
        return a.src.length - b.src.length;
      },
      sortDirections: ['descend'],
      ...getColumnSearchProps('src'),
    },
    {
      title: messages.engines.tgt,
      dataIndex: 'tgt',
      key: 'tgt',
      sorter: (a, b) => {
        return a.tgt.length - b.tgt.length;
      },
      sortDirections: ['descend'],
      ...getColumnSearchProps('tgt'),
    },
    {
      title: messages.engines.domain,
      dataIndex: 'domain',
      key: 'domain',
    },
    {
      title: messages.engines.flavor,
      dataIndex: 'flavor',
      key: 'flavor',
    },
    {
      title: messages.engines.model,
      dataIndex: 'inserviceid',
      key: 'inserviceid',
      // ...getColumnSearchProps('inserviceid'),
      render: (text, record) => {
        if (text === 26) return text;
        return '---';
      },
    },
    {
      title: messages.engines.status,
      dataIndex: 'status',
      key: 'status',
      render: text => {
        if (text === -2) return 'STATUS_NOED';
        if (text === -1) return 'STATUS_UNKNOWN';
        if (text === 0) return 'STATUS_READY';
        if (text === -4) return 'STATUS_BUSY';
        if (text === -5) return 'STATUS_OFF';
        return '---';
      },
    },
    {
      title: messages.default.actions,
      key: 'action',
      width: 100,
      render: (text, record) => {
        if (userId) {
          let exist = [];
          if (userEngines && userEngines.length) {
            exist = userEngines.filter(item => item.id === text.id);
          }

          return (
            <span>
              <Switch
                checked={exist.length ? true : false}
                loading={loading}
                onChange={() => {
                  userEng(record, exist.length);
                }}
              />
            </span>
          );
        } else {
          if (user.rol === 'admin') {
            return (
              <span>
                {record.status === 5 ? (
                  <Tooltip placement="top" title={messages.engines.enabled}>
                    <ButtonAction
                      onClick={() => {
                        enaEngine(record);
                      }}
                      ghost
                      size="small"
                      type="danger"
                      shape="circle"
                      icon="stop"
                    />
                  </Tooltip>
                ) : (
                  <Tooltip placement="top" title={messages.engines.disabled}>
                    <ButtonAction
                      onClick={() => {
                        disEngine(record);
                      }}
                      ghost
                      size="small"
                      type="primary"
                      shape="circle"
                      icon="check"
                    />
                  </Tooltip>
                )}
                <Popconfirm
                  onConfirm={() => {
                    delEngine(record);
                  }}
                  title={messages.engines.confirmDelete}
                  okText={messages.default.yes}
                  cancelText={messages.default.no}
                >
                  <Tooltip placement="top" title={messages.engines.delete}>
                    <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
                  </Tooltip>
                </Popconfirm>
              </span>
            );
          }
          if (user.rol === 'client') {
            return (
              <span>
                <Tooltip
                  placement="top"
                  title={user.rol === 'client' && !userId ? 'Grant All' : messages.default.granted}
                >
                  <Switch
                    checked={record.grant_all ? true : false}
                    onChange={() => {
                      grant(record);
                    }}
                  />
                </Tooltip>
              </span>
            );
          }
        }
      },
    },
  ];

  const clientOperations = (
    <React.Fragment>
      <Button
        loading={loading}
        onClick={() => {
          grant(1);
        }}
        type="primary"
        icon="check-circle"
      >
        Grant All
      </Button>
      <Button
        loading={loading}
        style={{ marginLeft: 5 }}
        onClick={() => {
          grant(0);
        }}
        type="danger"
        icon="close"
      >
        Deny All
      </Button>
    </React.Fragment>
  );

  const adminOperations = (
    <React.Fragment>
      {father && (
        <Button
          loading={loading}
          onClick={() => {
            setFather(null);
          }}
          style={{ marginRight: 20 }}
          size="small"
          type="dashed"
          icon="close"
        >
          {`Father (${father.id})`}
        </Button>
      )}

      <Button
        onClick={() => {
          setVisible(true);
          setMode('add');
        }}
        type="primary"
        icon="block"
      >
        {messages.engines.add}
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  return (
    <div>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>{messages.menus.engines}</Breadcrumb.Item>
        {userId && (
          <React.Fragment>
            <Breadcrumb.Item>{messages.menus.user}</Breadcrumb.Item>
            <Breadcrumb.Item>{userId}</Breadcrumb.Item>
          </React.Fragment>
        )}
      </Breadcrumb>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        {userId && (
          <Title level={4}>
            <Text underline>{user.rol === 'client' ? 'User' : 'Client'}</Text> Engine Management
          </Title>
        )}
        {userId && userActive && (
          <Row>
            <Col>
              <strong>
                <Icon type="user" /> FullName:{' '}
              </strong>
              {userActive.fullName.toUpperCase()}
            </Col>
            <Col>
              <strong>
                <Icon type="mail" /> Email:{' '}
              </strong>
              {userActive.email}
            </Col>
          </Row>
        )}
        {user.rol === 'client' && userId && !userActive && <Icon type="loading" />}
        <Tabs
          defaultActiveKey="all"
          tabBarExtraContent={user.rol === 'admin' ? (!userId ? adminOperations : null) : null}
        >
          <TabPane tab={messages.engines.all} key="all">
            {father && <span>existe</span>}
            {father ? (
              <Table
                rowKey="id"
                size="small"
                loading={loading}
                columns={columns}
                dataSource={children}
              />
            ) : (
              <Table
                rowKey="id"
                size="small"
                loading={loading}
                columns={columns}
                dataSource={engines}
              />
            )}
          </TabPane>
          {userId && (
            <TabPane tab={messages.engines.enables} key="enables">
              <Table
                rowKey="id"
                size="small"
                loading={loading}
                columns={columns}
                dataSource={userEngines}
              />
            </TabPane>
          )}
        </Tabs>

        <Modal
          centered
          width={600}
          title={messages.engines.add}
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.engines.service} hasFeedback>
                  {getFieldDecorator('serviceid', {
                    rules: [
                      {
                        required: true,
                        message: messages.engines.rules.service.required,
                      },
                    ],
                  })(
                    <Select
                      filterOption={(input, option) =>
                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                      }
                      loading={loadingServices}
                      showSearch
                    >
                      {services.map(item => {
                        return (
                          <Option key={item.id} value={item.id}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.engines.inserviceid} hasFeedback>
                  {getFieldDecorator('inserviceid', {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: messages.engines.rules.inserviceid.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label={messages.engines.src} hasFeedback>
                  {getFieldDecorator('src', {
                    rules: [
                      {
                        required: true,
                        message: messages.engines.rules.src.required,
                      },
                    ],
                  })(
                    <Select showSearch>
                      {languages.map(item => {
                        return (
                          <Option key={item.value} value={item.value.toLowerCase()}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.engines.tgt} hasFeedback>
                  {getFieldDecorator('tgt', {
                    rules: [
                      {
                        required: true,
                        message: messages.engines.rules.tgt.required,
                      },
                    ],
                  })(
                    <Select showSearch>
                      {languages.map(item => {
                        return (
                          <Option key={item.value} value={item.value.toLowerCase()}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.engines.descr} hasFeedback>
                  {getFieldDecorator('descr', {
                    rules: [
                      {
                        required: true,
                        message: messages.engines.rules.descr.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label={messages.engines.domain} hasFeedback>
                  {getFieldDecorator('domain')(<Input />)}
                </FormItem>
                <FormItem label={messages.engines.flavor} hasFeedback>
                  {getFieldDecorator('flavor')(<Input />)}
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};

const WrappedEngineCmp = Form.create()(injectIntl(EngineCmp));

export default withRouter(WrappedEngineCmp);
